import { Component, ViewChild, ElementRef } from '@angular/core';
import { LoadingController, AlertController, NavController } from '@ionic/angular';

import {SocketService} from '../../services/SocketService';
import Settings from '../settings';
import axios from 'axios';

export enum ValidationStep {
  NotYetValidated = 0,
  Validating = 1,
  Valid = 2,
  Invalid = 3
}

@Component({
  selector: 'claim-page',
  templateUrl: 'claim.page.html',
  styleUrls: ['claim.page.scss'],
})
export class ClaimPage {
    claimId: string = '';
    ValidationStep: typeof ValidationStep = ValidationStep;
    patientName: string = '';
    receipt: any = null;
    professional: string = '';
    service: string = '';
    date: Date = new Date();
    amount: number = 0.0;
    admissibleRefund: number = 0.0;
    providerValidationStep: ValidationStep = ValidationStep.NotYetValidated;
    locationValidationStep: ValidationStep = ValidationStep.NotYetValidated;
    serviceValidationStep: ValidationStep = ValidationStep.NotYetValidated;
    validatingSteps: boolean = false;
    settings: Settings = new Settings();
    formDisabled: boolean = false;
    submitButtonDisabled: boolean = true;
    refundButtonDisabled: boolean = true;
    submitButtonText: string = 'Remboursement';
    @ViewChild('image') fileInput:ElementRef;

    constructor(public loadingController: LoadingController, 
                public alertController: AlertController, 
                public navController: NavController,
                public socketService: SocketService) {

      this.socketService.watchAdmissibleRefund().subscribe((message: any) => {
        if (message.claimId === this.claimId){
          this.handleAdmissibleRefundValidation(message.isValid, message.errorMessage, message.admissibleRefund);
        }
      });

      this.socketService.watchLocationValidation().subscribe((message: any) => {
        if (message.claimId === this.claimId){
          this.handleLocationValidation(message.isValid, message.errorMessage);
        }
      });

      this.socketService.watchProviderValidation().subscribe((message: any) => {
        if (message.claimId === this.claimId){
          this.handleProviderValidation(message.isValid, message.errorMessage);
        }
      });

      this.socketService.watchServiceValidation().subscribe((message: any) => {
        if (message.claimId === this.claimId){
          this.handleServiceValidation(message.isValid, message.errorMessage);
        }
      });
    }
  
    pickImage(){
      this.fileInput.nativeElement.click();
    }

    async onFileDeposited(event: any) {
      navigator.geolocation.getCurrentPosition(async position => {
        this.submitButtonDisabled = true;
        this.refundButtonDisabled = true;

        const loader = await this.loadingController.create({
          message: 'Lecture du reçu',
          spinner: 'crescent',
        });

        loader.present();

        let formData = new FormData();
        formData.append('position', `${position.coords.latitude};${position.coords.longitude}`);
        formData.append('image', this.fileInput.nativeElement.files[0])
        let response = null;
        try {
          response = await axios.post(`${this.settings.backendUrl}/claims/ocr`, formData, { headers: {'Content-Type': 'multipart/form-data'} });
        
          this.claimId = response.data.claimId;
          this.amount = response.data.serviceAmount;
          this.date = response.data.serviceDate;
          this.professional = response.data.professionalName;
          this.service = response.data.professionalType;
          this.patientName = response.data.patientName;
          this.submitButtonDisabled = false;

        } catch (e) {
          const alert = await this.alertController.create({ 
            header: 'Error',
            message: e.response.data.error,
            buttons: ['OK']
          });
    
          await alert.present();
        }
        
        loader.dismiss();
      });
    }

    async formValidation() {
      this.submitButtonDisabled = true;
      this.refundButtonDisabled = true;
      const loader = await this.loadingController.create({
        message: 'Initialisation de la validation',
        spinner: 'crescent',
      });
      loader.present();

      const request = {
        claimId: this.claimId,
        patientName: this.patientName,
        professionalName: this.professional,
        serviceAmount: this.amount,
        serviceDate: this.date,
        professionalType: this.service,
      }

      try {
        
        await axios.post(`${this.settings.backendUrl}/claims/validate`, request);
        
        this.formDisabled = true;
        this.locationValidationStep = ValidationStep.Validating;
        this.providerValidationStep = ValidationStep.Validating;
        this.serviceValidationStep = ValidationStep.Validating;
      } catch (e) {
        const alert = await this.alertController.create({ 
          header: 'Error',
          message: e.response.data.error,
          buttons: ['OK']
        });
  
        await alert.present();
      }

      loader.dismiss();
    }

    async submitClaim() {
      const loader = await this.loadingController.create({
        message: 'Initialisation du remboursement',
        spinner: 'crescent',
      });
      loader.present();

      const request = {
        claimId: this.claimId
      }

      try {
        await axios.post(`${this.settings.backendUrl}/claims/submit`, request);
        const alert = await this.alertController.create({ 
          header: 'Succès',
          message: 'Remboursement effectué avec succès',
          buttons: ['OK']
        });
  
        await alert.present();
        
        this.resetForm();
      } catch (e) {
        const alert = await this.alertController.create({ 
          header: 'Error',
          message: e.response.data.error,
          buttons: ['OK']
        });
  
        await alert.present();
      }

      loader.dismiss();
    }

    async showAdminissibleRefundAlert(){
      const alert = await this.alertController.create({ 
        header: 'Information',
        message: 'Il est possible que le remboursement admissible soit inférieur que le montant total de votre service tout dépendant des limitations de votre politique d\'assurances',
        buttons: ['OK']
      });

      await alert.present();
    }

    async handleAdmissibleRefundValidation(isValid: boolean, errorMessage: string, adminissibleRefund: number){
      this.submitButtonDisabled = false;
      
      if (!isValid || errorMessage != null){
        await this.showError(errorMessage);
        this.formDisabled = false;
        return;
      }

      if (this.locationValidationStep === ValidationStep.Valid && this.serviceValidationStep === ValidationStep.Valid && this.providerValidationStep === ValidationStep.Valid) {
        this.submitButtonText = "Remboursement"
      }

      this.refundButtonDisabled = false;
      this.admissibleRefund = adminissibleRefund;
    }

    async handleProviderValidation(isValid: boolean, errorMessage: string){
      this.providerValidationStep = isValid ? ValidationStep.Valid : ValidationStep.Invalid;

      if (!isValid || errorMessage != null){
        this.submitButtonText = "Remboursement manuel";
        await this.showError(errorMessage);
      }
    }

    async handleLocationValidation(isValid: boolean, errorMessage: string){
      this.locationValidationStep = isValid ? ValidationStep.Valid : ValidationStep.Invalid;

      if (!isValid || errorMessage != null){
        this.submitButtonText = "Remboursement manuel";
        await this.showError(errorMessage);
      }
    }

    async handleServiceValidation(isValid: boolean, errorMessage: string){
      this.formDisabled = false;
      this.serviceValidationStep = isValid ? ValidationStep.Valid : ValidationStep.Invalid;

      if (!isValid || errorMessage != null){
        this.submitButtonText = "Remboursement manuel";
        await this.showError(errorMessage);
      }
    }

    private async showError(message){
      const error = await this.alertController.create({ 
        header: 'Erreur',
        message: message || 'Désolé, une erreur inattendue est survenue',
        buttons: ['OK']
      });

      await error.present();
    }

    resetForm(){
      this.claimId = '';
      this.patientName = '';
      this.receipt = null;
      this.professional = '';
      this.service = '';
      this.date = new Date();
      this.amount = 0.0;
      this.admissibleRefund = 0.0;
      this.providerValidationStep = ValidationStep.NotYetValidated;
      this.locationValidationStep = ValidationStep.NotYetValidated;
      this.serviceValidationStep = ValidationStep.NotYetValidated;
      this.validatingSteps = false;
      this.submitButtonDisabled = true;
      this.refundButtonDisabled = true;
    }
}
